import TextItem from './document.annotation.text.item.component';
import FormService from "../services/form.service";

export default class Form extends TextItem {

  constructor(props){
    super(props);
    this.state = { 
      notes:this.props.data.notes
    };

  }

    refreshNotes = () => {
    //rafraîchit le contenu de l'annotation
      FormService.get(this.props.data.id).then(
        (response) => {
          this.setState({
            notes:response.data.notes
          });
        },
        error => {
          if(error.response.status===401) this.props.history.push('/login');

          const resMessage =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();

          alert(resMessage);
        });
  }

  render() {
    console.log("render Form");
    console.log(this.state.notes);

    return (
      <TextItem 
      type="form" 
      id={this.props.data.id} 
      lang={this.props.data.kindOf} 
      text={this.props.data.text}
      notes={this.state.notes} 
      refresh={this.props.refresh}
      refreshNotes={this.refreshNotes}  
      available_lang={this.props.available_lang}
      note_available_lang={this.props.note_available_lang}
      />
    );
  }
};